<!-- Roboto all -->
<!-- <link href="https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i&display=swap" rel="stylesheet"> -->
<!-- Roboto 300, 400, 500 -->
<link href="https://fonts.googleapis.com/css?family=Roboto:300,400,500&display=swap" rel="stylesheet">

<script>
	export let darkMode;

	import Header from './Header.svelte';
	import HomeWide from './HomeWide.svelte';
	import HomeNarrow from './HomeNarrow.svelte';

	let winWidth;
	let winHeight;
	let resizeInProgress = false;
	let timeout = null;
	const mobileOperatingSystem = getMobileOperatingSystem();

	// https://stackoverflow.com/a/21742107/6586631
	function getMobileOperatingSystem() {
		let userAgent = navigator.userAgent || navigator.vendor || window.opera;
		// Windows Phone must come first because its UA also contains "Android"
		if (/windows phone/i.test(userAgent)) {
			return "windows_phone";
		}
		if (/android/i.test(userAgent)) {
			return "android";
		}
		if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
			return "ios";
		}
		return "unknown";
	}

	window.addEventListener("resize", updateWindowSize);
	function updateWindowSize() {
		resizeInProgress = true;
		clearTimeout(timeout);
		timeout = setTimeout(() => { resizeInProgress = false; }, 500);
	}
</script>

<svelte:window bind:innerWidth={winWidth} bind:innerHeight={winHeight}/>

<style>
	.outer {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		display: flex;
		flex-direction: row;
		justify-content: center;
		background-color: var(--background-color-light);
	}
	.outer.dark {
		background-color: var(--background-color-dark);
	}
	.container {
		font-family: 'Roboto', sans-serif;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		position: absolute;
		height: auto;
		bottom: 0;
		top: 0;
		padding: 20px;
		max-width: 1200px;
		color: var(--color-dark);
	}
	.container.dark {
		color: var(--color-light);
	}
	#overlay {
		z-index: 4;
		position: absolute;
		bottom: 0;
		top: 0;
		left: 0;
		right: 0;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		font-family: 'Roboto', sans-serif;
		color: var(--color-light);
		background-color: rgba(0, 0, 0, 0.75);
	}
	.box {
		border: 1px solid white;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-evenly;
		align-items: center;
		text-align: center;
		font-weight: 300;
		font-size: 18px;
		margin: 10px;
		background: 
			linear-gradient(
				to top left,
				rgba(0,0,0,0) 0%,
				rgba(0,0,0,0) calc(50% - 2px),
				white,
				rgba(0,0,0,0) calc(50% + 2px),
				rgba(0,0,0,0)
			),
			linear-gradient(
				to top right,
				rgba(0,0,0,0),
				rgba(0,0,0,0) calc(50% - 2px),
				white,
				rgba(0,0,0,0) calc(50% + 2px),
				rgba(0,0,0,0)
			);
	}
</style>

<div class="outer {darkMode ? 'dark' : ''}">
<div class="container {darkMode ? 'dark' : ''}">
	<Header {darkMode} narrow={winWidth <= 710}/>
	{#if winWidth > 710}
		 <HomeWide {darkMode}/>
	{:else}
		 <HomeNarrow {darkMode}/>
	{/if}
	<div/> <!-- empty div to center Home component via flex -->
</div>
</div>

{#if mobileOperatingSystem == 'unknown' && resizeInProgress}
	<div id="overlay">
		<div class="box">
			{winWidth}x{winHeight}
			<div/>
			<div/>
			<div/>
			{winWidth > 710 ? 'desktop' : 'mobile'}
		</div>
	</div>
{/if}
