<script>
    export let darkMode;
    export let narrow = false;
	const first = 'christoph';
    const last = 'schwizer';
    let dropdownArrowIcon = darkMode ? 'down-outline_dark.png' : 'down-outline.png';
    function showProjects() {
        let dropdownMenus = document.getElementsByClassName("dropdownContent");
        for (let i = 0; i < dropdownMenus.length; i++) {
            dropdownMenus[i].classList.toggle("hidden");
        }
        let dropdownArrows = document.getElementsByClassName("dropdownArrow");
        for (let i = 0; i < dropdownArrows.length; i++) {
            dropdownArrows[i].classList.toggle("unpressed");
        }
    }
    function onDropdownMouseOver() {
        dropdownArrowIcon = darkMode ? 'down-filled_dark.png' : 'down-filled.png';
    }
    function onDropdownMouseOut() {
        dropdownArrowIcon = darkMode ? 'down-outline_dark.png' : 'down-outline.png';
    }
</script>

<style>
    .container {
        display: flex;
        justify-content: space-between;
        font-weight: 300;
        font-size: 14px;
        letter-spacing: 0.22em;
    }
    .last {
        font-weight: 500;
    }
    .dropdown {
        display: inline-block;
    }
    .dropdownButton {
        letter-spacing: 0.2em;
        user-select: none;
    }
    .dropdownButton:hover {
        cursor: pointer;
    }
    .dropdownContent {
        z-index: 3;
        background-color: rgba(255, 255, 255, 0.5);
        border-top: 1px solid var(--background-color-dark);
        display: flex;
        flex-direction: column;
        position: absolute;
        right: 20px;
        overflow: auto;
        margin-top: 10px;
        transform: scaleY(1);
        transform-origin: top;
        transition: transform 200ms ease;
		-webkit-transition: transform 200ms ease;
		-moz-transition: transform 200ms ease;
		-ms-transition: transform 200ms ease;
		-o-transition: transform 200ms ease;
    }
    .dropdownContent.dark {
        border-top: 1px solid var(--background-color-light);
        background-color: rgba(0, 0, 0, 0.5);
    }
    .dropdownContent.hidden {
        transform: scaleY(0);
    }
    .dropdownContent > a {
        color: var(--color-dark);
        text-decoration: none;
        letter-spacing: 0.1em;
        padding: 15px;
    }
    .dropdownContent > a:hover {
        color: var(--color-light);
        background-color: var(--background-color-dark);
    }
    .dropdownContent.dark > a {
        color: var(--color-light);
    }
    .dropdownContent.dark > a:hover {
        color: var(--color-dark);
        background-color: var(--background-color-light);
    }
    .dropdownArrow {
        /* this is the pressed state */
        height: 10px;
        width: 10px;
        transform: rotate(180deg);
        transition: transform 300ms ease;
		-webkit-transition: transform 300ms ease;
		-moz-transition: transform 300ms ease;
		-ms-transition: transform 300ms ease;
		-o-transition: transform 300ms ease;
    }
    .dropdownArrow.unpressed {
        transform: rotate(0deg);
    }
</style>

<div class="container">
    <div class="logo">
    {#if narrow}
        <span class="first">{first}</span><br><span class="last">{last}</span>
    {:else}
        <span class="first">{first}</span><span class="last">{last}</span>
    {/if}
    </div>

    <div class="dropdown">
        <div class="dropdownButton" on:click={showProjects} on:mouseover={onDropdownMouseOver} on:mouseout={onDropdownMouseOut}>
            <img class="dropdownArrow unpressed" src="{dropdownArrowIcon}" alt="arrow"/> projects
        </div>
        <div class="dropdownContent hidden {darkMode ? 'dark' : ''}">
            <a href="https://github.com/chrisly-bear/PEBRApp" target="_blank" rel="noopener">PEBRApp</a>
            <a href="https://heartchart.app" target="_blank" rel="noopener">HeartChart</a>
            <a href="https://asvztracker.ch" target="_blank" rel="noopener">ASVZ Tracker</a>
            <a href="sunrise-usage" target="_blank">Sunrise Usage</a>
            <a href="https://github.com/chrisly-bear/time-machine-progress" target="_blank" rel="noopener">Time Machine Progress</a>
        </div>
    </div>
</div>