<script>
	import Main from './Main.svelte';
	
	let darkMode = false;
	let timeoutFunc;

	function toggleDarkMode() {
		document.getElementById("darkContainer").classList.toggle("collapsed");
		clearTimeout(timeoutFunc);
		if (darkMode) { /* switching to light mode */
			document.getElementById("darkContainer").style.clipPath = "circle(25% at 50% 27px)";
			document.getElementById("darkContainer").style['-webkit-clip-path'] = 'circle(25% at 50% 27px)';
			document.getElementById("mainLight").classList.add("shown");
		} else { /* switching to dark mode */
			document.getElementById("darkContainer").style.clipPath = "circle(150% at 50% 27px)";
			document.getElementById("darkContainer").style['-webkit-clip-path'] = 'circle(150% at 50% 27px)';
			document.getElementById("mainDark").classList.remove("hidden");
			timeoutFunc = setTimeout(() => {
				document.getElementById("mainLight").classList.remove("shown");
			}, 1000); // make sure the timeout is the same as the darkContainer's transition-duration
		}
		darkMode = !darkMode;
	}

	function onHover(e) {
		clearTimeout(timeoutFunc);
		if (!darkMode) {
			if (e.type === 'mouseover') {
				document.getElementById("darkContainer").style.clipPath = "circle(25% at 50% 27px)";
				document.getElementById("darkContainer").style['-webkit-clip-path'] = 'circle(25% at 50% 27px)';
				document.getElementById("mainDark").classList.remove("hidden");
			} else if (e.type === 'mouseout') {
				document.getElementById("darkContainer").style.clipPath = "circle(14px at 50% 27px)";
				document.getElementById("darkContainer").style['-webkit-clip-path'] = 'circle(14px at 50% 27px)';
				timeoutFunc = setTimeout(() => {
					document.getElementById("mainDark").classList.add("hidden");
				}, 1000); // make sure the timeout is the same as the darkContainer's transition-duration
			}
		} else {
			if (e.type === 'mouseover') {
			} else if (e.type === 'mouseout') {
			}
		}
	}
</script>

<style>
	#mainLight {
		z-index: 5;
		display: none;
	}
	#mainLight.shown {
		display: block;
	}
	#darkContainer {
		/* expanded state */
        z-index: 6;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
		clip-path: circle(150% at 50% 27px);
		-webkit-clip-path: circle(150% at 50% 27px);
		transition: all 1000ms ease;
		-webkit-transition: all 1000ms ease;
		-moz-transition: all 1000ms ease;
		-ms-transition: all 1000ms ease;
		-o-transition: all 1000ms ease;
		display: flex;
		justify-content: center;
		clip-path: circle(14px at 50% 27px);
		-webkit-clip-path: circle(14px at 50% 27px);
	}
	.darkModeIcon {
		z-index: 1;
		transform: rotate(45deg);
		height: 16px;
		width: 16px;
		margin-top: 9px;
		padding: 10px;
		background-color: var(--background-color-dark);
	}
	.darkModeIcon:hover {
		cursor: pointer;
	}
	#mainDark.hidden {
		display: none;
	}
	#mainDark {
		display: block;
	}
</style>

<div id="mainLight" class="shown">
	<Main darkMode={false}/>
</div>
<div id="darkContainer" class="collapsed">
	<img class="darkModeIcon" src="darkmode.png" alt="dark mode" on:mouseover={onHover} on:mouseout={onHover} on:click={toggleDarkMode}/>
	<div id="mainDark" class="hidden">
		<Main darkMode={true}/>
	</div>
</div>
